@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@1,900&family=Roboto+Condensed&family=Roboto+Mono:ital,wght@1,300&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

input:-webkit-autofill,
.name input:-webkit-autofill:active,
.name input:-webkit-autofill:focus,
.name input:-webkit-autofill:hover {
  -webkit-transition: background-color 5000s ease-in-out 0s;
  transition: background-color 5000s ease-in-out 0s;
}

html,
body,
#root {
  min-height: 100vh;
}

body {
  font-size: 1rem;
  margin: 0;
  font-family: "Roboto Condensed", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body #root {
  height: 100%;
}

.App {
  text-align: center;
}


.create-acc-container {
  background: url(data:image/png;base64,) left bottom,
    url(/gasme-fe-pjt-3/static/media/topleft.f57af1b5.png) top left,
    url(data:image/png;base64,) top right;
  background-repeat: no-repeat;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0px 10px;
  min-height: 100vh;
}

.create-acc-container > * {
  width: 100%;
  max-width: 1500px;
}

.create-acc-container .top {
  padding-top: 20px;
}

.create-acc-container .body {
  flex: 1 1;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.create-acc-container .content {
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  max-width: 600px;
  margin-top: 30px;
}

.create-acc-container .content > * {
  margin-bottom: 20px;
}

.create-acc-container .address input,
.create-acc-container .number input,
.create-acc-container .email input,
.create-acc-container .bus-name input,
.create-acc-container .pass input,
.create-acc-container .name input:focus {
  outline: none;
}

.create-acc-container .title {
  text-align: center;
}

.create-acc-container .title h2 {
  font-size: 2rem;
}

/* .create-acc-container .address input , */
.create-acc-container .name input,
.create-acc-container .number input,
.create-acc-container .email input {
  border: 0px;
  font-size: 1.3rem;
  padding: 15px;
  width: 100%;
  background: #d1e6db;
  border-radius: 8px;
}

/* .create-acc-container .address, */
.create-acc-container .name,
.create-acc-container .number,
.create-acc-container .email {
  position: relative;
}

/* .create-acc-container .bus-name , */
.create-acc-container .pass {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background: #d1e6db;
  border-radius: 8px;
  padding-right: 10px;
  position: relative;
}
/* .create-acc-container .bus-name input, */
.create-acc-container .pass input {
  border: 0px;
  background: #d1e6db;
  flex: 1 1;
  padding: 15px;
  font-size: 1.3rem;
  border-radius: 8px;
}

.create-acc-container .mark {
  color: #37b12c;
  font-weight: bold;
}

/* .create-acc-container .bus-name .mark > span {
  color: red;
} */

.create-acc-container .terms {
  text-align: center;
  font-size: 1rem;
}

.create-acc-container .terms input[type="checkbox"] {
  margin-right: 5px;
  border: 0px;
}

.create-acc-container .google,
.create-acc-container .create {
  display: flex;
  justify-content: center;
}

.create-acc-container .create button {
  background-color: #37b12c;
  color: #fff;
  padding: 13px 73px;
  border: 0px;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 7px;
  display: flex;
  align-items: center;
}

.create-acc-container .google button {
  display: flex;
  align-items: center;
  background-color: #fff;
  color: #000;
  padding: 13px 50px;
  border: 1px solid #37b12c;
  font-size: 0.7rem;
  border-radius: 7px;
}

.create-acc-container .invalid-input {
  line-height: 1;
  position: absolute;
  font-size: 0.88rem;
  right: 50%;
  color: red;
  bottom: -17px;
}

.banner {
  display: flex;
  flex-direction: column;
  padding: 0 80px;
  position: relative;
}

.banner::after {
  background: rgba(0, 0, 0, 0.6) url(/gasme-fe-pjt-3/static/media/bannerImg.c4d55ff7.png);
  background-blend-mode: darken;
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;

  content: "";
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: -1;
}

.banner .top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-top: 40px;
  z-index: 2;
}

.banner .new-menu {
  position: absolute;
  visibility: hidden;
}

.banner .top .logo {
  margin-right: auto;
}

.banner .top nav {
  height: 40px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 350px;
  margin-right: 100px;
}

.banner .top nav li {
  list-style: none;
}
.banner .top nav li a {
  font-size: 1.2em;
  letter-spacing: 1px;
  font-weight: bold;
  cursor: pointer;
  text-decoration: none;
  color: #fff;
}

.banner nav li a:hover {
  border-bottom: 3px solid #10e048;
  font-size: 1.3em;
}

.banner .top .buttons button {
  font-size: 1em;
  padding: 10px 30px;
  border-radius: 5px;
  font-weight: bold;
  color: #fff;
  background-color: #299445;
  border: 1px solid #076327;
  transition: background-color 400ms ease-in-out;
  cursor: pointer;
}

.banner .buttons .login {
  margin-right: 15px;
}

.banner .buttons .login-link {
  text-decoration: none;
  color: #fff;
}

.banner .top .buttons button:hover {
  background-color: #deeee5;
  color: #299445;
}

.banner .top .menu {
  visibility: hidden;
}

.banner .body {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  z-index: 2;
}

.banner .big-text h1 {
  color: #ffe81b;
  font-size: min(max(1rem, 4vw), 4.5em);
  margin-top: 90px;
}

.banner .body .small-text {
  color: #fff;
  font-size: min(max(0.7rem, 2vw), 1.5em);
  margin-top: 100px;
  font-weight: bold;
}

.banner .body .search {
  display: flex;
  background-color: #fff;
  border-radius: 7px;
  padding: 15px 20px;
  margin-top: 100px;
  margin-bottom: 108px;
  max-width: 600px;
  cursor: pointer;
}

.banner .body .search input[type="text"] {
  border: 0px;
  font-size: 1.5em;
  width: 100%;
}

.banner .body .search input[type="text"]::-webkit-input-placeholder {
  font-size: 1em;
}

.banner .body .search input[type="text"]:-ms-input-placeholder {
  font-size: 1em;
}

.banner .body .search input[type="text"]::placeholder {
  font-size: 1em;
}
.banner .body .search input[type="text"]:focus {
  outline: none;
}

.banner .body .search button {
  background-color: #37b12c;
  border: 0px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  font-size: 1.2em;
  padding: 6px 15px;
  color: #fff;
}

/* media queries */
@media only screen and (max-width: 1200px) {
  .banner .top nav {
    display: none;
  }

  .banner .top .buttons {
    display: none;
  }

  .banner .top .menu {
    visibility: visible;
    border: 0px;
    border-radius: 7px;
    cursor: pointer;
  }
  /* new menu */
  .banner .new-menu {
    visibility: visible;
    position: absolute;
    background-color: #4d4d4d;
    left: 0;
    top: 0;
    right: 0;
    z-index: 8;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    align-items: flex-start;
    -webkit-transform: translatey(-290px);
            transform: translatey(-290px);
    transition: -webkit-transform 0.3s ease-in;
    transition: transform 0.3s ease-in;
    transition: transform 0.3s ease-in, -webkit-transform 0.3s ease-in;
  }

  .banner .new-nav {
    padding: 0px 100px;
    margin: 0px;
    display: flex;
    flex-direction: column;
    flex: 1 1;
  }

  .banner .new-nav li {
    border-bottom: 1px solid #9e9c9a;
    margin: 0px;
    list-style: none;
  }

  .banner .new-nav li a:hover {
    background-color: #464343;
    font-weight: bold;
  }

  .banner .new-nav li a {
    display: block;
    font-size: 1.2em;
    padding: 8px 0px;
    text-decoration: none;
    color: #fff;
    letter-spacing: 2px;
    font-weight: 100;
    width: 100%;
    height: 100%;
    transition: background-color 500ms ease-in-out;
  }

  .banner .menu {
    background-color: transparent;
    border: 0px;
    border-radius: 7px;
    cursor: pointer;
  }
}

@media only screen and (max-width: 800px) {
  .banner .body .search {
    padding: 7px 15px;
    margin-top: 50px;
    max-width: 600px;
  }
  .banner .body .search input[type="text"]::-webkit-input-placeholder {
    font-size: 0.7em;
  }
  .banner .body .search input[type="text"]:-ms-input-placeholder {
    font-size: 0.7em;
  }
  .banner .body .search input[type="text"]::placeholder {
    font-size: 0.7em;
  }
  .banner .body .search button {
    align-items: center;
    font-size: 1em;
    padding: 3px 10px;
    color: #fff;
  }

  .banner .body div input[type="text"] {
    font-size: 1.2em;
  }

  .banner {
    padding: 0 40px;
  }
}

