  left: 0;
  top: 0;
  right: 0;
  z-index: 1;
}

.login-container .slide-menu .menu-items {
  display: flex;
  justify-content: flex-end;
  flex: 1 1;
}

.login-container .menu-items ul {
  display: flex;
  flex-direction: column;
  width: 90%;
}

.login-container .menu-items ul li {
  border-bottom: 1px solid #9e9c9a;
  margin: 0px;
  list-style: none;
}

.login-container .slide-menu li a {
  display: block;
  font-size: 1.2em;
  padding: 8px 0px;
  text-decoration: none;
  color: #fff;
  letter-spacing: 2px;
